#root {
  width: 100vw;
  height: 100vh;
  background-color: rgb(71, 71, 71);
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  width: 390px;
  height: 763px;
  padding: 31px 31px 0px 31px;
  position: relative;
  background-color: white;
  box-sizing: border-box;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 32px;
  text-decoration: none;
}

main {
  height: 524px;
  overflow: scroll;
  overflow-x: hidden;
  scrollbar-width: thin;
}

::-webkit-scrollbar {
  display: none;
}
